\n      display: flex;\n      align-items: center;\n      i{\n        color: $primary-color;\n        font-size: 30px;\n        margin-right: 3px;\n      }\n      span{\n        margin: 10px;\n        transition: all .50s ease;\n        color: $text-color;\n        font-size: 1.7rem;\n        font-weight: 600;\n      }\n\n    }\n    .navbar{\n      display: flex;\n      a{\n        display: block;\n        min-height: 45px;\n        color: $text-color;\n        font-size: 1.1rem;\n        font-weight: 500;\n        padding: 5px 0;\n        margin: 0px 30px;\n        transition: all .50s ease;\n        &:hover{\n          color: $primary-color;\n        }\n        &.active{\n          color: $primary-color;\n        }\n      }\n    }\n    .main{\n      display: none;\n      align-items: center;\n      a{\n        display: flex;\n        align-items: center;\n        min-height: 45px;\n        margin-right: 25px;\n        margin-left: 10px;\n        color: $text-color;\n        font-size: 1.1rem;\n        font-weight: 500;\n        transition: all .50s ease;\n        &:hover{\n          color: $primary-color;\n        }\n      }\n      .user{\n        display: flex;\n        align-items: center;\n        i{\n          color: $primary-color;\n          font-size: 28px;\n          margin-right: 7px;\n        }\n      }\n      #hamburgerButton{\n        font-size: 40px;\n        color: $text-color;\n        cursor: pointer;\n        z-index: 10001;\n        display: none;\n      }\n    }\n  }\n  main{\n    color: $bg-color;\n  }\n}\n@media screen and (max-width: 1500px){\n  body{\n    header{\n      padding: 14px 2%;\n      .navbar{\n        a{\n          height: 45px;\n          font-size: 1rem;\n          padding: 14px 0;\n          margin: 0px 20px;\n        }\n      }\n    }\n  }\n}\n@media screen and (max-width: 880px) {\n  body{\n    .customer-reviews{\n      grid-template-columns: 1fr 1fr;\n    }\n    .container-details{\n      margin: 20px;\n      display: block;\n    }\n    .restaurant-image-details{\n      width: 100%;\n    }\n    .card-container{\n      grid-template-columns: repeat(2, 1fr);\n    }\n    .hero{\n      // min-height: 70vh;\n      transition: all .50s ease;\n      .hero__title{\n        font-size: 2rem;\n      }\n      .hero__tagline{\n        font-size: 1rem;\n      }\n      picture{\n        height: 50vh;\n      }\n    }\n    header{\n      .main{\n        display: flex;\n        #hamburgerButton{\n          border: none;\n          display: block;\n          background-color: transparent;\n        }\n      }\n      .navbar{\n        position: absolute;\n        top:100%;\n        right: -100%;\n        width: 270px;\n        height: 35vh;\n        background-color: $primary-color;\n        display: flex;\n        flex-direction: column;\n        justify-content: flex-start;\n        border-radius: 10px;\n        transition: all .50s ease;\n        &.open{\n          right: 2%;\n        }\n        a{\n          display: block;\n          margin: 12px 0;\n          padding: 0 25px;\n          transition: all .50s ease;\n          &:hover{\n            color: $text-color;\n          }\n          &.active{\n            color: $text-color;\n          }\n        }\n      }\n    }\n  }\n  .container-details{\n    display: block;\n  }\n  .restaurant-image-details{\n    width: 100%;\n  }\n}\n@media screen and (max-width : 520px){\n  body{\n    .customer-reviews{\n      grid-template-columns: 1fr;\n    }\n    header{\n      .logo{\n        span{\n          transition: all .50s ease-in-out;\n          font-size: 1rem;\n        }\n      }\n      .main{\n        a{\n          margin-right: 10px;\n          font-size: .8rem;\n        }\n      }\n    }\n    .card-container{\n      grid-template-columns: repeat(1, 1fr);\n    }\n    .hero{\n      .hero__title{\n        font-size: 1.5rem;\n      }\n      .hero__tagline{\n        font-size: .7rem;\n      }\n    }\n  }\n}\n.hero {\n  justify-content: center;\n  // margin-top: 5%;\n  transition: all .50s ease;\n  display: flex;\n  align-items: center;\n  // min-height: 80vh;\n  // width: 100%;\n  text-align: center;\n  // background-color: #222327ac;\n  picture{\n    width: 100%;\n    height: 80vh;\n    img{\n      width: 100%;\n      height: 100%;\n      object-fit: cover;\n    }\n  }\n}\n\n.hero__inner {\n  position: absolute;\n  margin: 0 auto;\n  max-width: 800px;\n}\n\n.hero__title {\n  color: $primary-color;\n  font-weight: 600;\n  font-size: 3rem;\n}\n\n.hero__tagline {\n  color: $primary-color;\n  margin-top: 10px;\n  font-size: 18px;\n  font-weight: 500;\n}\n\n.card-container {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 20px;\n  padding: 20px;\n  max-width: 1200px;\n  margin: 0 auto;\n}\n\n.restaurant-item {\n  background-color: #fff;\n  border-radius: 10px;\n  overflow: hidden;\n  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;\n  transition: all .50s ease;\n}\n.restaurant-item:hover {\n  transform: scale(1.05); /* Perbesar sel ketika dihover */\n  transition: transform .50s ease-in-out; \n}\n\n.restaurant-item img {\n  width: 100%;\n  height: 200px;\n  object-fit: cover;\n}\n\n.card-content {\n  color: $bg-color;\n  padding: 15px;\n}\n\n.detail-button{\n  height: 20px;\n  display: flex;\n  justify-items: baseline;\n  justify-content: flex-end;\n  a{\n    color: $bg-color;\n    font-weight: 600;\n  }\n}\n\n.restaurant-name {\n  display: flex;\n  justify-content: space-between;\n  font-size: 1.2em;\n  font-weight: 600;\n  margin-bottom: 4px;\n}\n\n.rating {\n  color: #f8d028;\n  font-size: 1.1em;\n  margin-bottom: 8px;\n}\n\n.location {\n  font-size: 1em;\n  color: #555;\n}\n.description{\n  margin-top: 10px;\n  font-size: .8em;\n}\nfooter{\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  height: 60px;\n  background-color: $bg-color;\n}\n.skip-link {\n  width: 120px;\n  min-width: 45px;\n  position: absolute;\n  top: -40px;\n  left: 10px;\n  padding: 8px;\n  background-color: $primary-color;\n  color: $bg-color;\n  &:focus{\n    top: 0;\n    z-index: 10002;\n  }\n}\n.container-details {\nmargin-top: 10%;\ndisplay: flex;\nalign-items: center;\nmax-width: 1000px;\nmargin: 20px auto;\nbackground-color: #fff;\nborder-radius: 8px;\nbox-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;\noverflow: hidden;\n}\n\n.restaurant-image-details {\nwidth: 50%;\nmax-height: 500px;\nobject-fit: cover;\n}\n\n.restaurant-details {\npadding: 20px;\n}\n\n.restaurant-name-details {\nfont-size: 24px;\nfont-weight: bold;\nmargin-bottom: 10px;\n}\n\n.rating-details {\ncolor: #FFD700; /* warna emas */\nfont-size: 18px;\nmargin-bottom: 10px;\n}\n.header-details{\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.location-details {\nfont-size: 16px;\ncolor: #555;\nmargin-bottom: 10px;\n}\n\n.description-details {\nfont-size: 16px;\nline-height: 1.5;\ncolor: #333;\n}\n.menu-details{\n  ul{\n    display: flex;\n    flex-wrap: wrap;\n  }\n  li{\n    margin: 0 10px 0 10px;\n    width: auto;\n    list-style: circle;\n  }\n}\n.review-container{\n  max-width: 1300px;\n  color: $primary-color;\n  background-color: $bg-color;\n  margin: 20px auto;\n  padding: 20px;\nborder-radius: 8px;\nbox-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;\n}\n.customer-reviews{\n  gap: 20px;\n  display: grid;\n  grid-template-columns: 1fr 1fr 1fr;\n}\n.customer-info{\n  strong{\n    font-size: 1.2rem;\n  }\n  span{\n    color: #555;\n  }\n}\n.customer-review{\n  color: $bg-color;\n  padding: 10px;\n  border-radius: 8px;\n  background-color: $text-color;\n}\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n  \n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  \n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n.header-palsu{\n  height: 15vh;\n}\n.restaurant-item__not__found{\n  height: 65vh;\n}"],"sourceRoot":""}]);
